<template>
	<section>
		<h2>{{result}}</h2>
		<div class="search">
			<input type="text" v-model="dateStart" maxlength="8" placeholder=") 20201201" @keyup.enter="getData">
			<input type="text" v-model="dateEnd" maxlength="8" placeholder=") 20201202" @keyup.enter="getData">
			<button @click="getData">click</button>
		</div>
		<div v-for="(row, idx) in filtered" :key="idx">
			<p> : {{row.stateDt}}</p>
			<p> : {{row.stateTime}}</p>
			<p>  : {{row.decideCnt}}</p>
			<p>   : {{row.new}}</p>
			<p>  : {{row.clearCnt}}</p>
			<p>  : {{row.examCnt}}</p>
			<p>  : {{row.deathCnt}}</p>
			<p>   : {{row.careCnt}}</p>
			<p>   : {{row.resutlNegCnt}}</p>
			<p>   : {{row.accExamCnt}}</p>
			<p>    : {{row.accExamCompCnt}}</p>
			<p>  : {{row.accDefRate}}</p>
		</div>
	</section>
</template>

<script>
export default {
	data() {
		return {
			result: 'covid',
			dateStart : null,
			dateEnd : null,
			list : null,
			filtered : null,
			today : null
		}
	},
	methods: {
		sort() {
			var totalList = this.list;
			var dateStart = Number(this.dateStart);
			var dateEnd = Number(this.dateEnd);
			if(dateEnd == 0) dateEnd = this.today;
			this.filtered = totalList.filter(function(item, idx){
				var list;
				if(item.stateDt >= dateStart && item.stateDt <= dateEnd) {
					list = item;
					if(totalList[idx-1] != undefined) {
						item['new'] = totalList[idx-1].decideCnt - totalList[idx].decideCnt;
					}
				}
				return list;
			});
		},
		getData() {
			if(this.list === null) {
				var today = new Date();
				var year = String(today.getFullYear());
				var month = String(today.getMonth() + 1);
				var date = String(today.getDate());
				if(date < 10) date = '0'+date;
				this.today = Number(year + month + date);
				this.$axios.get('/sample/data.json')
				.then((res) => {
					const data = JSON.parse(res.data);
					this.list = data.response.body.items.item;
					this.sort();
				})
				.then((err) => {
					console.log(err);
				});
			} else {
				this.sort();
			}
			
		}
	}
}
</script>